import { Component, OnInit } from '@angular/core';
import {NewsService} from '../news.service'

@Component({
  selector: 'app-stocks',
  templateUrl: './stocks.component.html',
  styleUrls: ['./stocks.component.css'],
  providers:[NewsService]
})
export class StocksComponent implements OnInit {
k;
tday;
d;
stocks:any=[]
  constructor(private stock:NewsService) { }

  ngOnInit() {
    this.getdate();
    this.k = setInterval(() => {
      this.getdate(); 
    }, 1000);

  this.getstock();
  }
  getstock(){
    this.stock.showstock().subscribe(data=>this.stocks.push(data))
    console.log(this.stocks)
  }
  getdate(){
    this.tday=new Date();
 
  }
}
