import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import { AppComponent } from './app.component';
import {FormsModule} from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { HomeComponent } from './home/home.component'; 
import { NewsComponent } from './news/news.component';
import { StocksComponent } from './stocks/stocks.component';
import { ContactComponent } from './contact/contact.component';
import { LoginComponent } from './login/login.component';
import { PortfolioComponent } from './portfolio/portfolio.component';
import { SupportComponent } from './support/support.component';
import { CurrencyPipe } from './currency.pipe';
import { CountrycodePipe } from './countrycode.pipe';
import { ConvertPipe } from './convert.pipe';


const appRoutes: Routes=[
{path:'', component:HomeComponent},
{path:'home', component:HomeComponent},
{path:'news', component:NewsComponent},
{path:'stocks', component:StocksComponent},
{path:'contact', component:ContactComponent},
{path:'login', component:LoginComponent},
{path:'portfolio', component:PortfolioComponent},
{path:'support', component:SupportComponent},
{path:'profile', redirectTo:'home'},
{path:'**', redirectTo:'/'}
]
@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,NewsComponent,StocksComponent,ContactComponent,LoginComponent, PortfolioComponent, SupportComponent, CurrencyPipe, CountrycodePipe, ConvertPipe

  ],
  imports: [
    BrowserModule, RouterModule.forRoot(appRoutes) ,HttpClientModule, FormsModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
